/* header styling */
.tap2playlogo {
    width: 4.5em;
}

.headerTitle {
    font-weight: bold;
    font-size: 2.5em;
    color: #51504C;
}

.header {
    display: flex;
    justify-content: center;
    margin: 1.5em;
}

/* learning zone styling */
.title {
    font-weight: bold;
    font-size: 1.2em;
    color: #51504C;
    margin-left: 1.5em;
}

.learningCards {
    display: flex;
    justify-content: space-evenly;
    margin: 1em 0em;
}

.learningCards2 {
    margin-top: 1.3em;
}

.learningCard:active {
    background-color: #158ace;
    box-shadow: 0.3em 0.3em 0.6em gray;
    transform: scale(1.02);
    transition: 0.1s;
}

.learningCards img {
    width: 8.5em;
    height: 6em;
    border-radius: 15px;
    margin: 1em;
}

.learningCard {
    background-color: #43A7E0;
    border-radius: 20px;
    width: 10.5em;
    height: 10.5em;
    box-shadow: 0.3em 0.3em 0.6em gray;
}

.learningCardTitle {
    color: white;
    font-weight: 500;
    font-size: 1.2em;
    text-align: center;
}

/* how it works styling */
.videoContainer {
    display: flex;
    justify-content: center;
    margin: 1em;
}

.videoContainer video {
    width: 95%;
    border-radius: 20px;
}

/* information styling */
.infoCards {
    display: flex;
    justify-content: space-evenly;
    margin: 1em 0em;
    color: white;
}

.infoCard {
    background-color: #F7C359;
    border-radius: 20px;
    width: 10.5em;
    height: 13em;
    box-shadow: 0.3em 0.3em 0.6em gray;
}

.infoCard:active {
    background-color: #ca8f18;
    box-shadow: 0.3em 0.3em 0.6em gray;
    transform: scale(1.02);
    transition: 0.1s;
}

.dot {
    height: 5em;
    width: 5em;
    background-color: white;
    border-radius: 200%;
    margin: 1em;
    
    /* center image */
    display: block;
    margin-left: auto;
    margin-right: auto;

    display: flex;
    justify-content: center;
    align-items: center;
  }

.infoCard img {
    width: 2em;
    height: 2em;
}

.infoCardTitle {
    color: white;
    font-weight: 500;
    font-size: 1.2em;
    text-align: center;
}

.infoCardText {
    font-size: 0.9em;
    text-align: center;
    padding: 0.4em;
}

/* why adults should tap2play */
.adultsCard {
    background-color: #43A7E0;
    border-radius: 20px;
    width: 21.5em;
    margin: 2em 0em;
    box-shadow: 0.3em 0.3em 0.6em gray;
    color: white;

    margin: auto;
    width: 90%;
    padding: 0.3em;
    margin-bottom: 1em;
    margin-top: 1.3em;
}

.adultsCardTitle {
    font-weight: 700;
    font-size: 1.2em;
    padding: 0.7em 0.7em 0em 0.7em;
}

.adultsCardText {
    font-size: 1em;
    padding: 1em;
}

/* media queries */
@media screen and (min-width: 500px) {
    .main {
        margin: 2% 10%;
        border: 2px solid black;
        border-radius: 20px;
    }
}

@media screen and (min-width: 600px) {
    .main {
        margin: 2% 18%;
        border: 2px solid black;
        border-radius: 20px;
    }
}

@media screen and (min-width: 800px) {
    .main {
        margin: 2% 25%;
        border: 2px solid black;
        border-radius: 20px;
    }
}

@media screen and (min-width: 1000px) {
    .main {
        margin: 2% 30%;
        border: 2px solid black;
        border-radius: 20px;
    }
}

@media screen and (min-width: 1200px) {
    .main {
        margin: 2% 35%;
        border: 2px solid black;
        border-radius: 20px;
    }
}

@media screen and (min-width: 1500px) {
    .main {
        margin: 2% 35%;
        border: 2px solid black;
        border-radius: 20px;
    }
}